<template>
  <div>
    <PersonalHeader></PersonalHeader>
    <div class="centerBg">
      <div class="wrapper clearfix">
        <div class="centerLeft">
          <PersonalMenu class="fl" currentMenu="5"></PersonalMenu>
        </div>
        <div class="centerRight">
          <div class="centerTitle">
            <ul>
              <li class="active">我的足迹</li>
            </ul>
          </div>
          <div class="footContent">
            <dl class="clearfix" v-for="item in data" :key="'foot'+item.id">
              <router-link :to="'/detail?id='+item.id">
                <dt><img :src="item.img"></dt>
                <dd>
                  <div class="title between">
                    <h4>{{item.item_name}}</h4>
                    <b>￥{{item.money}}万</b>
                  </div>
                  <p class="desc">
                    <span>所属行业：{{item.cate_name}}</span>
                    <span>直营店：{{item.shop_count}}家</span>
                    <span>申请人数：{{item.read_num}}</span>
                    <span>加盟店：{{item.fran_store_num}}家</span>
                  </p>
                </dd>
              </router-link>
            </dl>
          </div>

          <el-pagination class="paging" background layout="prev, pager, next" :total="count"  @current-change="pageChange"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api  from "@/api/personal-api"
  export default {
    name: "index.vue",
    data(){
      return{
        data:'',
        page: 0,
        count: 0
      }
    },
    created(){
      this.getData();
    },
    methods: {

      getData(){
        const {page} = this;
        api.getProductList({type:3,page}).then(data=>{
            this.data = data.data;
            this.count = data.count;
        });
      },

      /**
       * 点击分页
       * @param e
       */
      pageChange:function(e){
        this.page = e-1;
        window.scrollTo(0,0);
        this.getData();
      },
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  .centerBg{
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }
  .centerLeft{
    float: left;
    width: 230px;
  }
  .centerRight{
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;
    .centerTitle{
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      ul{
        li{
          height: 58px;
          line-height: 58px;
          float: left;
          color: #333;
          font-size: 16px;
          border-bottom: 2px solid #fff;
        }
        li.active{
          border-bottom: 2px solid #4F74F4;
        }
      }
    }
    .footContent{
      padding: 0 30px;
      dl{
        padding: 20px 0;
        border-bottom: 1px dashed #E3E4E5;
        dt{
          float: left;
          width: 140px;
          height: 140px;
          img{
            display: block;
            width: 100%;
            height: 100%;
          }
        }
        dd{
          float: right;
          width: 730px;
          .title{
            height: 26px;
            line-height: 26px;
            font-size: 18px;
            margin: 20px 0;
            h4{
              color: #333;
            }
            b{
              color: #FF7E00;
            }
          }
          .desc{
            width: 330px;
            span{
              float: left;
              width: 160px;
              line-height: 28px;
              color: #999;
            }
          }
        }
      }
    }
  }
  .paging{
    margin: 30px;
    text-align: center;
  }
</style>



